import React, { Component } from "react";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { connect } from "react-redux";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import { createNumberMask } from "redux-form-input-masks";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});
const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

class FormCash extends Component {
  constructor(props) {
    super(props);
    this.state = {
      kodeBarang: [],
      kodeJenis: "",
      namaJenis: "",
      kadarCetak: "",
      kadarBeli: "",
      columns: [
        {
          dataField: "tunaiRp",
          text: "Jumlah(Rp)",
        },
        {
          dataField: "tunaiKurs",
          text: "Harga",
        },
        {
          dataField: "tunaiNetto",
          text: "Netto (Gr)",
        },
      ],
      columnsTransfer: [
        {
          dataField: "transferRp",
          text: "Jumlah(Rp)",
        },
        {
          dataField: "transferKurs",
          text: "Harga",
        },
        {
          dataField: "transferNetto",
          text: "Netto (Gr)",
        },
      ],
      columnsGiro: [
        {
          dataField: "giroRp",
          text: "Jumlah(Rp)",
        },
        {
          dataField: "giroKurs",
          text: "Harga",
        },
        {
          dataField: "giroNetto",
          text: "Netto (Gr)",
        },
      ],
      tampungCiok: [],
    };
  }

  onChange() {
    this.props.change("netto", this.props.netto);
  }

  render() {
    switch (this.props.from) {
      case "cash":
        return (
          <form onSubmit={this.props.handleSubmit} className="col-lg-12">
            <div className="row">
              <div className="col-lg-3 form-group">
                <label htmlFor="">Jumlah</label>
                <Field
                  name="bruto"
                  component="input"
                  type="tel"
                  className="form-control"
                  onBlur={this.onChange()}
                  {...currencyMask}
                />
              </div>
              <div className="col-lg-3 form-group">
                <label htmlFor="">Harga</label>
                <Field
                  name="kadarTransaksi"
                  component="input"
                  type="tel"
                  className="form-control"
                  onBlur={this.onChange()}
                  {...currencyMask}
                />
              </div>
              <div className="col-lg-3 form-group">
                <Field
                  name="netto"
                  label="Netto"
                  component={renderField}
                  type="number"
                  className="form-control"
                  onBlur={this.onChange()}
                />
              </div>
              <div className="col-lg-12 mb-3">
                <div className="text-right">
                  <button
                    className="btn btn-primary"
                    type="submit"
                    disabled={this.props.submitting}
                  >
                    Simpan
                  </button>
                </div>
              </div>
              {this.props.data ? (
                <ToolkitProvider
                  keyField="id"
                  data={this.props.data}
                  columns={this.state.columns}
                  search
                >
                  {(props) => (
                    <div className="form-group">
                      <BootstrapTable
                        {...props.baseProps}
                        striped
                        hover
                        condensed
                        bordered={true}
                        headerClasses="header-table"
                        pagination={paginationFactory()}
                      />
                    </div>
                  )}
                </ToolkitProvider>
              ) : null}
            </div>
          </form>
        );
      case "transfer":
        return (
          <form onSubmit={this.props.handleSubmit} className="col-lg-12">
            <div className="row">
              <div className="col-lg-3 form-group">
                <label htmlFor="">Jumlah</label>
                <Field
                  name="bruto"
                  component="input"
                  type="tel"
                  className="form-control"
                  onBlur={this.onChange()}
                  {...currencyMask}
                />
              </div>
              <div className="col-lg-3 form-group">
                <label htmlFor="">Harga</label>
                <Field
                  name="kadarTransaksi"
                  component="input"
                  type="tel"
                  className="form-control"
                  onBlur={this.onChange()}
                  {...currencyMask}
                />
              </div>
              <div className="col-lg-3 form-group">
                <Field
                  name="netto"
                  label="Netto"
                  component={renderField}
                  type="number"
                  className="form-control"
                  onBlur={this.onChange()}
                />
              </div>
              <div className="col-lg-12 mb-3">
                <div className="text-right">
                  <button
                    className="btn btn-primary"
                    type="submit"
                    disabled={this.props.submitting}
                  >
                    Simpan
                  </button>
                </div>
              </div>
              {this.props.data ? (
                <ToolkitProvider
                  keyField="id"
                  data={this.props.data}
                  columns={this.state.columnsTransfer}
                  search
                >
                  {(props) => (
                    <div className="form-group">
                      <BootstrapTable
                        {...props.baseProps}
                        striped
                        hover
                        condensed
                        bordered={true}
                        headerClasses="header-table"
                        pagination={paginationFactory()}
                      />
                    </div>
                  )}
                </ToolkitProvider>
              ) : null}
            </div>
          </form>
        );
      case "giro":
        return (
          <form onSubmit={this.props.handleSubmit} className="col-lg-12">
            <div className="row">
              <div className="col-lg-3 form-group">
                <label htmlFor="">Jumlah</label>
                <Field
                  name="bruto"
                  component="input"
                  type="tel"
                  className="form-control"
                  onBlur={this.onChange()}
                  {...currencyMask}
                />
              </div>
              <div className="col-lg-3 form-group">
                <label htmlFor="">Harga</label>
                <Field
                  name="kadarTransaksi"
                  component="input"
                  type="tel"
                  className="form-control"
                  onBlur={this.onChange()}
                  {...currencyMask}
                />
              </div>
              <div className="col-lg-3 form-group">
                <Field
                  name="netto"
                  label="Netto"
                  component={renderField}
                  type="number"
                  className="form-control"
                  onBlur={this.onChange()}
                />
              </div>
              <div className="col-lg-12 mb-3">
                <div className="text-right">
                  <button
                    className="btn btn-primary"
                    type="submit"
                    disabled={this.props.submitting}
                  >
                    Simpan
                  </button>
                </div>
              </div>
              {this.props.data ? (
                <ToolkitProvider
                  keyField="id"
                  data={this.props.data}
                  columns={this.state.columnsGiro}
                  search
                >
                  {(props) => (
                    <div className="form-group">
                      <BootstrapTable
                        {...props.baseProps}
                        striped
                        hover
                        condensed
                        bordered={true}
                        headerClasses="header-table"
                        pagination={paginationFactory()}
                      />
                    </div>
                  )}
                </ToolkitProvider>
              ) : null}
            </div>
          </form>
        );

      default:
        break;
    }
  }
}

FormCash = reduxForm({
  form: "FormCash",
  enableReinitialize: true,
})(FormCash);
const selector = formValueSelector("FormCash"); // <-- same as form name
export default connect((state) => {
  const { bruto, kadarTransaksi } = selector(state, "bruto", "kadarTransaksi");
  return {
    netto: (parseFloat(bruto) / parseFloat(kadarTransaksi)).toFixed(3),
  };
})(FormCash);
